import background from './assets/background.jpeg';
import cpp from './assets/cpp.png';
import opengl from './assets/opengl.png';
import cuda from './assets/cuda.png';
import htmlcss from './assets/csshtml.svg';
import js from './assets/js.png';
import php from './assets/php.png';
import reactimage from './assets/react.png';
import mps from './assets/mps.png';
import mpsdem from './assets/mps-dem.png';
import mujo from './assets/mujo.png';
import laptime from './assets/laptime.png';
import kaaphal from './assets/kaaphal.png';
import kaaphalapp from './assets/kaaphalapp.png';
import myphoto from './assets/myphoto.jpg';
import mypic from './assets/mypic.jpg';
import resume_raman from './assets/resume_raman.pdf'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import { faHome } from '@fortawesome/free-solid-svg-icons'
import { faProjectDiagram } from '@fortawesome/free-solid-svg-icons'
