      <div className="App-intro">
        <div>
        <img src={myphoto} className="App-myphoto" alt="My" />
        </div>
        <div className="App-intro-textcontainer">
          <div className="App-hi">
          Hi!
          </div>
          <div className="App-myname">
          I am Raman Ghimire. 
          </div>
          <div className="App-mybio"> 
          A Software Developer especializing in developing software for simulations and visualization along with NVIDA CUDA programming.
           Aditioanlly I make Web Apps and Mobile Apps in React and React-Native.
          </div> 
          <a href={resume_raman} download>
          <div className="App-cv">
            Download CV
          </div>
          </a>
         
        </div>
        
        </div>
        
