      <div className="App-scrolldown">
      <FontAwesomeIcon icon={faAngleDoubleDown} onClick={()=>console.log(projectsRef.current.scrollIntoView({behavior: "smooth", block: "start", inline: "nearest"}))  } /> 
      </div>
